<template>
  <div id="result-text">
    <pre>{{$store.getters.centsSaved}}</pre>
    <h1>{{$t('successMsg.header')}}</h1>
    <div class="alert alert-success" role="alert">
      <i18n path="successMsg.text" tag="p">
        <span place="days-smoke-free" class="hl-number">{{$store.getters.duration.asDays() | toInt}}</span>
        <span place="seconds-smoke-free" class="hl-number">{{$store.state.ticks.ticks}}</span>
        <span place="money-saved" class="hl-number">{{$store.getters.moneySaved | formatDigits}}</span>
        <span place="cigarettes-saved" class="hl-number">{{$store.getters.cigarettesSaved | formatDigits}}</span>
      </i18n>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ResultText',
  methods: {
  },
  filters: {
    formatDigits (value, digits = 4) {
      const power = Math.pow(10, digits)
      return (Math.round(value * power) / power).toFixed(digits)
    },
    toInt (value) {
      return Math.round(value)
    },
    // TODO (21.11.18) make work
    formatSeparator (value) {
      return value.toLocaleString()
    }
  }
}
</script>

<style scoped>
  .hl-number {
    color: #DE1B2E;
  }
</style>
